@import url(https://fonts.googleapis.com/css?family=Exo+2:300,400,500,600,700,800,900&display=swap&subset=cyrillic);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Exo 2', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.App{position:relative;width:100%;height:100vh;background:url("/img/1945.jpg") top center no-repeat;background-size:cover;z-index:0}.App::before{position:absolute;content:"";left:0;top:0;right:0;bottom:0;background:rgba(0,0,0,0.8)}.App span{position:absolute;text-align:center;left:50%;top:50%;height:auto;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);font-size:24px;color:#ffffff;font-weight:600}

